import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faXmark } from '@fortawesome/free-solid-svg-icons'
import "../Style/AddImgBlock.scss"
import { useState } from 'react';

export default function AddImgBlock({ imageList,item, setPreviewUrl, setImageList, index}) {
    const [showDeleteImgIcon, setShowDeleteImgIcon] = useState(false);


    const handleChangeShowImg = (e)=>{
        setPreviewUrl(e.target.src);
      }
      const deleteImgListItem = ()=>{
        const newImageList = [...imageList];
        newImageList.splice(index, 1); 
        setImageList(newImageList); 
        if(index === 0) index++;
        else index--;
        setPreviewUrl(imageList[index]);

      }
  return (
        <div className='addMoreImgsItem'>
            <img onMouseOut={()=>setShowDeleteImgIcon(false)} onMouseOver={()=>setShowDeleteImgIcon(true)} onClick={handleChangeShowImg} className="inseryImgItem" src={item} alt='Tour Photo'></img>
            {showDeleteImgIcon? 
                <div className='deleteAddImgIcon' onClick={deleteImgListItem} onMouseOut={()=>setShowDeleteImgIcon(false)} onMouseOver={()=>setShowDeleteImgIcon(true)}>
                    <FontAwesomeIcon icon={faXmark} className="navIcons" />
                </div>
                :""
            }
        </div>
  )
}
