import React, {useState, useRef, useEffect} from 'react'
import he from 'he';
import AddTimeDetail from '../Component/AddTimeDetail'
import Warning from '../UtilComponent/Warning.js';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faPlus, faImage, faXmark, faSpinner } from '@fortawesome/free-solid-svg-icons'
import { faTrashCan } from '@fortawesome/free-regular-svg-icons'
import { getFetch, getArrBySeq } from "../CommonFunction/commonFunction.js" 
import {  notShowAddEventBlock  } from '../features/handleBoolean/toggleBlock.js';
import {  fetchMainPage  } from '../features/handleBoolean/toggleFetchData.js';
import {  addNewTimeDetail , removeTimeDetail,updateToDefault  } from '../features/handleInput/inputObj.js';
import { updateWarnText, showWarnBlock, setExternalFunction,hideScroll } from '../features/handleBoolean/toggleWarnBlock.js';
import {  useDispatch, useSelector } from 'react-redux';



import "../Style/AddEvent.scss"
import AddImgBlock from '../Component/AddImgBlock.js';


export default function AddEventBlock() {
    const tagRef = new useRef("");
    const [previewUrl, setPreviewUrl] = useState(null);
    const [imageList, setImageList] = useState([]);
    const [imageSizeList, setImageSizeList] = useState([]);
    const debounceTimer = useRef(null);
    const [saving, setSaving] = useState(false);
    const [ifImgLoading, setIfImgLoading] = useState(false);
    const [formData, setFormData] = useState({name: '',tags: '',img: '',event:[]});
    const [tags, setTags] = useState([]);
    const [steps, setSteps] = useState(1);
    const timeDetail = useSelector((state) => state.inputObj.timeDetail);
    const ifShowWarnBlock = useSelector((state) => state.toggleWarnBlock.ifShowWarnBlock);
    const screenWidth = useSelector((state) => state.screenSize.screenWidth);
    const smallScreen900 = screenWidth <= 900;


    const dispatch = useDispatch();

    const removeNewTimeDetail = ()=>{
        if(timeDetail.length === 1) return;
        dispatch(removeTimeDetail());
    }

    const handleInputChange = (e)=>{
        const { name, value } = e.target;
        setFormData({
          ...formData,
          [name]: value,
        });    
        let tagHint = "";
        let ifFetchTagHing = false;
        for(let i = value.length-1; i >= 0; i--){
            let char = value[i];
            if(char !== "#" && char !== " "){
                tagHint = char + tagHint;
            }
            else if(char === " "){
                ifFetchTagHing = false;
                break;
            }else if(char === "#"){
                ifFetchTagHing = true;
                break;
            }
        }
        if(tagHint && ifFetchTagHing){
            let ifFullChar = true;
            if (/[\u4E00-\u9FA5]/.test(tagHint[tagHint.length-1])) {
                ifFullChar = true;
            } else if (/[\u3100-\u312F\u31A0-\u31BF\u4E00-\u9FA5]/.test(tagHint[tagHint.length-1])) {
                // 注音
                ifFullChar = false;
            }
            if(ifFullChar){
                clearTimeout(debounceTimer.current);
                debounceTimer.current = setTimeout(() => {
                    getFetch("https://ap9.ragic.com/js1031222/tour/4?api&v=3&subtables=0&limit=6&offset=0&where=1000143,regex,^"+tagHint).then(result=>{
                        setTags(getArrBySeq(result));
                })
                }, 500); 
            }
        }else{
            setTags([]);
        }
    }
    const removeAddEvent = () =>{
        setTimeout(() => {
            if(saving) return;
            document.querySelector("html").style.overflowY = "auto";
            dispatch(notShowAddEventBlock());
        }, 100);
    } 

    const uploadImg = (e)=>{
        if(steps !== 2) setSteps(2);
       setIfImgLoading(true);
       setTimeout(() => {
        let file = e.target.files[0];
        var reader = new FileReader();
        reader.onload = function(e) {
            let  img = new Image();
            setImageList(prevList => [...prevList, e.target.result]);
            setPreviewUrl(e.target.result);
            setIfImgLoading(false);
            img.onload = function() {
                setImageSizeList(prevList => [...prevList, img.width / img.height]);
            };
            img.src = e.target.result;
       };
       reader.readAsDataURL(file);
       }, 500);
    }

  const saveAddEvent = ()=>{
    if(saving) return;
    //empty check
    if(!formData["name"]){
        dispatch(updateWarnText("請輸入行程名稱"));
        dispatch(showWarnBlock(true));
        dispatch(hideScroll(true));
        setExternalFunction(null);
        return;
    }
    let emptySubName = timeDetail.find(item=>item["name"].length === 0);
    if(emptySubName){
        dispatch(updateWarnText("請輸入地點"));
        dispatch(showWarnBlock(true));
        dispatch(hideScroll(true));
        setExternalFunction(null);
        return;
    }
    setSaving(true);


    setTimeout(() => {
        let imgList = document.querySelectorAll(".inseryImgItem");
        async function convertImagesToBlob(imgList){
            const promises = [];
            for(let i = 0; i < imgList.length; i++){
                let imgItem = imgList[i];
                const canvas = document.createElement('canvas');
                const ctx = canvas.getContext('2d');
                canvas.width = 900;
                canvas.height = 900 / imageSizeList[i];
                ctx.drawImage(imgItem, 0, 0, canvas.width, canvas.height);

                const promise = new Promise(resolve => {
                    canvas.toBlob(blob => {
                      resolve(new File([blob], "testing.webp", { type: "image/webp" }));
                    },'image/webp', 0.7);
                  });
              
                promises.push(promise);
            }
            const blobs = await Promise.all(promises);
            return blobs;
        }

        convertImagesToBlob(imgList).then(res=>{
            //tags organize
            let tagsStr = formData["tags"];
            let isTagStr = false;
            let finalTagStr = "";
            for(let i = 0; i < tagsStr.length; i++){
            let str = tagsStr[i];
            if(str === "#"){
                isTagStr = true;
            }else if(str === " "){
                isTagStr = false;
            }
            if(isTagStr) finalTagStr += str;
            }
            const submitFormData = new FormData();

            submitFormData.append("1000130", he.escape(formData["name"]));
            submitFormData.append("1000132", he.escape(finalTagStr));
            res.forEach(item=>{
                submitFormData.append("1000148", item);   
            })
            submitFormData.append("1000140", 0);
            submitFormData.append("1000141", 0);

            for(let i = 1; i <= timeDetail.length; i++){
              const item = timeDetail[i-1];
              submitFormData.append("1000134_"+-i,he.escape(item["time"]));
              submitFormData.append("1000135_"+-i,he.escape(item["type"]));
              submitFormData.append("1000136_"+-i,he.escape(item["name"]));
              submitFormData.append("1000137_"+-i,he.escape(item["content"]));
            }

            fetch("https://ap9.ragic.com/js1031222/tour/2?api&v=3", {
              "method":"post",
              "headers":new Headers({
                  "Authorization": "Basic aHdkVEdYblovMzY4aWk0bDlXVEhyakJJOE9NSjU1T0tnaktWYnJ5elpNS2FLTjU2QWNwUGdweFhGalRBVVFxcw=="
              }),
              "body": submitFormData
              }).then(res => res.json())
              .then(data => {
                  if(data["status"] === "SUCCESS") {
                      localStorage.setItem(data["ragicId"]+"_owner", 1);
                      setSaving(false);
                      removeAddEvent();
                      dispatch(fetchMainPage());
                      dispatch(updateToDefault());
                  }
              })
        });
   }, 500);
  }
  

  return (  
    <>
        <div className='addEventBlock'  style={steps === 2 && !smallScreen900? {width:"800px"}:{}} >
            <div className='detailTitle'>
                <div className='cancelAddNewEvent' onClick={removeAddEvent} style={saving?{cursor:"not-allowed"}:{cursor:"pointer"}}>取消</div>
                <div >新增行程</div>
                {steps === 2? <div className='saveAddNewEvent' onClick={saveAddEvent}>
                               {saving? <div className='saveLoading'></div>:""}
                                儲存
                              </div>
                    :""
                } 
            </div>
            <input accept='.jpg,.png,.webp' onChange={uploadImg} id="fileInput" type="file" style={{display: "none"}}></input>
            {steps === 1? 
                <div className='addEventBlockContainer'>
                        <div className='imgUploadContainer'>
                            <FontAwesomeIcon icon={faImage} className="navIcons" />
                            <h3>來上傳一張行程照片吧!!</h3>
                            <label htmlFor="fileInput">從電腦選擇</label>
                        </div>
                </div>
                :null
            }
            {steps === 2? 
               <div className='addEventDetailContainer'>
                    <div className='savingBackground' style={saving?{display:"block"}:{display:"none"}}></div>
                        <div className='imgUploadContainer'>
                            <div className="spinner"></div>
                            {previewUrl? <img id='insertImg' src={previewUrl} alt='Tour Photo'></img>:""}
                            {previewUrl? 
                                <div className='addMoreImgsContainer'>
                                    {ifImgLoading?                                    
                                    imageList.map((item, index)=>{
                                      return  <AddImgBlock index={index}  setPreviewUrl={setPreviewUrl} item={item}/>
                                    }):
                                    imageList.map((item, index)=>{
                                      return  <AddImgBlock index={index} setPreviewUrl={setPreviewUrl} imageList={imageList} item={item} setImageList={setImageList}/>
                                    })}
                                    {ifImgLoading?  <div className='addMoreImgsItem'>                            
                                                        <FontAwesomeIcon style={{fontSize:"15px"}} icon={faSpinner} className="navIcons" spin />
                                                    </div>:""}
                                    <div className='addMoreImgsItemIcon'>
                                        <label htmlFor="fileInput2">
                                                <input accept='.jpg,.png,.webp' onChange={uploadImg} id="fileInput2" type="file" style={{display: "none"}}></input>
                                                <FontAwesomeIcon icon={faPlus} className="navIcons" />
                                        </label>
                                    </div>
                                </div>:""}
                        </div>
                    <div className='addEventDetail'>
                        <div className='insertTitleContainer'>
                            <input name='name' onChange={handleInputChange} value={formData.name} type='text' placeholder='行程名稱'></input>
                        </div>
                        <div className='insertTagContainer'>
                            <input ref={tagRef} type='text' placeholder='新增標籤 : #台北 #餐廳' name='tags' onChange={handleInputChange} value={formData.tags}></input>
                            <div className="autocompleteList" style={tags.length === 0? {display:"none"}:{display:"block"}}>
                                <div className='autocompleteListContainer'>
                                    {tags.map(item=>{
                                        if(item){
                                            return <div>{item["tag"] + "   -   " +item["count"] +"則"}</div>
                                        }
                                    })}
                                </div>
                            </div>
                        </div>
                        {timeDetail.map((item,index)=>{
                            return <AddTimeDetail data={item} index={index}/>
                        })}
                        <div className='addNewTourIconContainer'>
                            <div className="addNewTourContent" onClick={()=>dispatch(addNewTimeDetail())}>
                                <FontAwesomeIcon icon={faPlus} className="navIcons" />
                            </div>
                            <div className="addNewTourContent" onClick={ removeNewTimeDetail} style={timeDetail.length === 1? {cursor:"not-allowed"}:{}}>
                                <FontAwesomeIcon icon={faTrashCan} className="navIcons" />
                            </div>
                        </div>
                    </div>
                </div>:null
            }
        </div>
        <div onClick={removeAddEvent} className='background' >
              <div onClick={removeAddEvent} className='xmarkContainer'>
                  <FontAwesomeIcon icon={faXmark} className="navIcons" />
              </div>
        </div>
        {ifShowWarnBlock? <Warning  />:null}
    </>
  )
}
